<template>
    <span @click="scrollToTop()" :class="[isShow?'fadeInRight':'fadeOutRight']" class='upbox  animated delay-2s slower '
        style="position: fixed;
        top: 80%;
        right: 1%;
        cursor: pointer;
        height: 3rem;
        width: 3rem;
        border-radius: 2px;
        color: white;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        background: #363938;overflow:hidden;">
        <i class="iconfont icon-shang  upBtn" :class="[isShow?'fadeInRight':'fadeOutRight']"
            style="font-size:15px;color:white;"></i>

    </span>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import axios from "axios";

export default {
    name: "upBtn",
    components: {},
    data() {
        return {
            isShow: false
        };
    },
    mounted() {
        ///this.handleScroll()
        console.log('upbtn mounted...')
        window.addEventListener('scroll', this.handleScroll, true);
    },
    methods: {
        ...mapActions(["getGrxx", "checkLogin", "logout", "getPermissions"]),
        handleScroll() {
            // 页面滚动距顶部距离
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop ||
                document.body.scrollTop
            var scroll = scrollTop - this.i;
            this.i = scrollTop;
            if (scroll < 0) {
                this.isShow = false
            } else {
                this.isShow = true
            }
        },
        scrollToTop() {
            this.scrollAnimation(document.documentElement.scrollTop || document.body.scrollTop, 0)
        },
        scrollAnimation(currentY, targetY) {
            // 获取当前位置方法
            // const currentY = document.documentElement.scrollTop || document.body.scrollTop
            // 计算需要移动的距离
            let needScrollTop = targetY - currentY
            let _currentY = currentY
            setTimeout(() => {
                // 一次调用滑动帧数，每次调用会不一样
                const dist = Math.ceil(needScrollTop / 10)
                _currentY += dist
                window.scrollTo(_currentY, currentY)
                // 如果移动幅度小于十个像素，直接移动，否则递归调用，实现动画效果
                if (needScrollTop > 10 || needScrollTop < -10) {
                    this.scrollAnimation(_currentY, targetY)
                } else {
                    window.scrollTo(_currentY, targetY)
                }
            }, 1)
        },
    },
    computed: {
        ...mapGetters(["isLogin", "user", "progress"])
    }
};
</script>

<style lang="less" scoped>
@import "../../assets/common.less";
@keyframes movetoup {
    0% {
        top: -114%;
    }

    50% {
        top: -185%;
    }

    51% {
        top: -43%;
    }

    100% {
        top: -114%;
    }
}

.upbox {
    &:hover .upBtn {
        animation: movetoup 1.5s infinite !important;
    }
}

.upBtn {
    font-size: 18px;
    color: #f78286;
    top: -114%;
    position: absolute;
}

.img_content {
    height: 320px;
    width: 100%;
    justify-content: center;
    display: flex;
    img {
        height: 100%;
        width: auto;
        -webkit-transition: all 0.4s;
        transition: all 0.4s;
    }
}
</style>



